body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input.form-item-empty {
	border-bottom: 2.5px solid rgb(157, 45, 45);
}

/*DELETE*/
.vhc-ticket-load-screen,.vhc-load-screen,.vhc-save-load-screen, .vhc-email-load-screen{
	position:fixed;
	width:100%;
	height:100%;
	z-index: 400;
  }
  
  /*Container for adding contextual items to load screen like text, etc*/
  .vhc-load-screen-item-cont {
	background-color: white;
	height: 80%;
	width: 80%;
	margin-left: 10%;
	margin-right: 10%;
	margin-top: 5%;
	margin-bottom: 5%;
	border-radius: 10px;
  
	display: grid;
	align-items: center;
	justify-content: center;
  }
  
  /*Image size must be a ratio of 1920 by 1080*/
  .vhc-load-screen-img{
	width: 640px;
	height: 360px;
	z-index: 100;
  }
  
  /*Dash*/
  .vhc-ticket-load-screen>.vhc-load-screen-item-cont {
	height: 40vh;
	width: 50vw;
	margin-top: 45vh;
	margin-left: 40vw;
  }
  
  .vhc-ticket-load-screen>.vhc-load-screen-item-cont>.vhc-load-screen-img {
	width: 320px;
	height: 180px;
  }
  
  /*Save*/
  .vhc-save-load-screen>.vhc-load-screen-item-cont {
	height: 30vh;
	width: 35vw;
	margin-top: 32.5vh;
	margin-left: 32.5vw;
  }
  
  .vhc-save-load-screen>.vhc-load-screen-item-cont>.vhc-load-screen-img {
	width: 160px;
	height: 90px;
  }
  
  /*Email*/
  .vhc-email-load-screen>.vhc-load-screen-item-cont {
	height: 30vh;
	width: 40vw;
	margin-top: 35vh;
	margin-left: 30vw;
  }
  
  .vhc-email-load-screen>.vhc-load-screen-item-cont>.vhc-load-screen-img {
	width: 320px;
	height: 180px;
  }
  
  .noshow {
	background-color: rgba(0, 0, 0, 0.5);
	position: absolute;
	z-index: 100;
	top:0;
	left:0;
  }
  
  .shadow {
	filter: drop-shadow(1px 2px 3px rgba(0, 0, 0, 0.5));
  }
  
  .vhc-load-screen-text {
	text-align: center;
	font-size: 30px;
  }
  
  /*Animation from: https://codepen.io/thetallweeks/pen/yybGra*/
  .vhc-load-screen-text::after {
	overflow: hidden;
	display: inline-block;
	vertical-align: bottom;
	-webkit-animation: ellipsis steps(4,end) 2000ms infinite;      
	animation: ellipsis steps(4,end) 2000ms infinite;
	content: "\2026"; /* ascii code for the ellipsis character */
	width: 0px;
  }
  
  @keyframes ellipsis {
	to {
	  width: 1.25em;    
	}
  }
  
  @-webkit-keyframes ellipsis {
	to {
	  width: 1.25em;    
	}
  }