/*CSS styling for the past report card*/

/*RUN REPORT CARD*/
/*======================================*/
#spiff-report-card {
	filter: unset;
	border: 1px solid #ccc;
}
#spiff-report-card>.card-menubar {
	position: relative;
}

/*MONTHLY REPORT VIEW*/
/*======================================*/
#report-view-card {
	filter: unset;
	border: 1px solid #ccc;
}
#report-view-card>.card-menubar {
	position: relative;
}
.monthly-report-closedate {
	text-align: left;
}
.monthly-report-closedate>div {
	font-size: 20px;
	font-style: italic;
}

/*PAST REPORT CARD*/
/*======================================*/
#spiff-past-report-card {
	filter: unset;
	border: 1px solid #ccc;
}
#spiff-past-report-card>.card-menubar {
	position: relative;
}
#spiff-past-report-card>.card-content {
	padding: 0;
}
#table-reports {
	padding-top: 12.5px;
	height: 70vh;
}

#table-reports>.tablerow {
	grid-template-columns: 17.5% 20% 20% 17.5% 25%;
	align-items: center;
	height: calc((71vh - 120px) / 12);
	padding: 0;
	align-content: center;
}

#table-reports>.tablerow:hover {
	background-color: #ccc;
}


#spiff-past-report-card-card-content>.tablerow.header {
	text-align:center;
	background-color: #103b61 !important;
	color:#fff;
	font-weight: 600;
	font-size: 16px;
	height: 50px;
	padding: 0px;
	margin: 0;
	justify-content: center;
	grid-template-columns: 17.5% 20% 20% 17.5% 25%;
}

#spiff-past-report-card-card-content>.tablerow.header>div {
	background-color: #103b61;
	height: 50px;
	margin: 0px;
	padding: 0px;
	text-align: center;
	align-items: center;
	justify-content: center;
	display: grid;
}

.report-table-header-label {
	text-align: center;
	padding: 0;
	margin: 0px !important;
	width: 90%;
}

/*BUTTONS*/
.past-report-print {
	background-color: rgb(153, 144, 104) !important;
}

.past-report-print:hover {
	background-color: #ada47a !important;
}

.past-report-view {
	background-color:seagreen !important;
}

.past-report-view:hover {
	background-color: rgb(70, 170, 113) !important;
}

/*SPIFF REPORT CARD
===========================
*/
/*Containers*/
.report-cont {
    display: flex;
    position: absolute;
    top:1.5vh;
}

#spiff-past-report-card {
    min-height: 71vh;
    max-height: 71vh;
    max-width: 30vw;
    min-width: 30vw;
    margin-left:1.5vw;
    margin-right:1.5vw;
    margin-top: 1.5vh;
}
#spiff-report-card {
	position: relative;
    min-height: 20vh;
    max-height: 20vh;
    max-width: 30vw;
    min-width: 30vw;
    margin-left:1.5vw;
    margin-right:1.5vw;
	z-index:100;
}
#spiff-report-card>.card-content {
    height: calc(100% - 40px);
}
#spiff-report-form {
    height: 70%;
    display: grid;
    grid-template-columns: 30% 40% 30%;
    align-items: center;
    justify-content: center;
    row-gap: 10px;
    column-gap: 10px;
	font-size: 15px;
	margin-top: 10px;
	padding-top: 0;
}
#spiff-report-actions {
    display: flex;
    justify-content: center;
    gap:10px;
	padding-top: 0px;
}
#spiff-report-actions>.action-row {
	padding-top: 0px;
    width: 75%;
}
#spiff-report-actions>.action-button {
    width: calc(30% - 10px);
}

/*Form Item Customizations*/
#spiff-report-form>.form-info-item {
    display: flex;
    justify-content: flex-start;
}
#spiff-report-form>.form-info-item>:first-child {
    width: 45%;
    font-size: 15px;
    font-weight: 525;
}
#spiff-report-form>.form-info-item>:nth-child(2){
    width: 50%;
}
#spiff-report-form>.form-info-item>input {
	height: 30px;
}
/*#spiff-report-form>label>div>.dropdown-list {
   
}*/

/*Report Action Button Customizations*/
#report-action-run {
    background-color:rgb(62, 121, 189);
}
#report-action-run:hover {
    background-color:rgb(105, 153, 209);
}
#report-action-print {
    background-color:rgb(141, 130, 81);
}
#report-action-print:hover {
    background-color:rgb(153, 144, 104);
}
#report-action-view {
    background-color:rgb(129, 81, 141);
}
#report-action-view:hover {
    background-color:rgb(164, 113, 177);
}
#report-action-refresh {
    background-color:rgb(69, 146, 110);
}
#report-action-refresh:hover {
    background-color:rgb(91, 167, 131);
}

.tech-spiff-summary {
	height: 80vh;
	overflow-y: overlay;
}

.comm-type-cont {
	display: grid;
	grid-template-columns: 50% 50%;
	gap:10px !important;
	margin: 0;
	padding: 0;
}


.comm-type-form {
	display: flex;
	flex-direction: column;
	margin: 0;
	width: 90%;
}
.comm-type-form>.form-info-item {
	display: grid;
	grid-template-columns: 90% 10%;
	width: 100%;
	font-size: 12.5px;
}
.comm-type-form>.form-info-item>:nth-child(2) {
	text-align: left;
	font-weight: 700;
	font-size: 20px;
}
.comm-type-form>.form-info-item>.form-info-title {
	overflow:visible;
	display:flex;
	text-align: left;
}
#lost-comm-type, #appr-comm-type {
	display: grid;
}
.comm-type-cont>.card-content {
	display: flex;
	flex-direction: column;
	padding: 0;
}
#lost-comm-type>div, #appr-comm-type>div {
	font-size: 20px;
	text-align: left;
	padding: 0;
}
#report-comm-summary {
	display: grid;
	height: fit-content;
}

/*Windows 125%/50% Scaling CSS*/
@media screen and (max-width: 1550px) {
	#report-view-card {
		min-height: 89vh !important;
		max-height: 89vh !important;
	}
	#spiff-report-card {
		min-height: fit-content;
		max-height: fit-content;
	}

	#spiff-report-card>.card-menubar,
	#spiff-past-report-card>.card-menubar,
	#report-view-card>.card-menubar {
		height: 30px;
		line-height: 12.5px;
	}
	#report-view-card>.card-content {
		max-height: calc(100% - 40px);
	}
	.tech-spiff-summary {
		height: 75vh;
	}
	#spiff-report-form {
		height: 70%;
		display: grid;
		grid-template-columns: repeat(2, 50%);
		align-items: center;
		justify-content: center;
		row-gap: 4px;
		column-gap: 5px;
		padding-bottom: 0px;
	}
	#spiff-report-card-card-content {
		padding-top: 0px;
		padding-bottom: 0px;
	}
	#spiff-report-form>.form-info-item>.form-info-title {
		font-size: 12.5px;
	}
	#spiff-report-form>.form-info-item>input {
		height: 20px;
		border-radius: 5px;
		font-size: 12.5px;
	}
	#report-run-checkbox-cont,
	#report-cleaned-checkbox-cont,
	#report-run-checkbox,
	#report-cleaned-checkbox {
		height: 20px;
	}
	#report-run-checkbox,
	#report-cleaned-checkbox {
		width: 20px !important;
	}
	#spiff-report-actions>.action-button,
	#spiff-report-actions>.action-row>.action-button {
		height: 25px;
		line-height: 25px;
	}
	#spiff-report-form>.form-info-item>.dropdown-cont,
	#spiff-report-form>.form-info-item>.dropdown-cont>.dropdown-option.drop-down.selected {
		height: 20px;
		font-size: 10px;
	}
	#spiff-report-actions {
		padding-top: 7.5px;
		padding-bottom: 7.5px;
	}

	#report-view-actions {
		padding: 0px;
		height: 22.5px;
	}
	#report-view-actions>.dropdown-cont,
	#report-view-actions>.dropdown-cont>.dropdown-option.drop-down.selected,
	#report-view-actions>.action-button {
		height:100%;
	}
	#report-view-actions>.action-button {
		line-height: 30px !important;
		font-size: 10px !important;
		width: 100px;
	}

	#spiff-past-report-card {
		min-height: 63vh;
		max-height: 63vh;
	}
	#table-reports {
		padding-top: 0px;
		padding-bottom: 0px;
	}


	#spiff-past-report-card-card-content>.tablerow.header>div {
		font-size: 11px;
		height: 30px;
	}
	#spiff-past-report-card-card-content>.tablerow.header {
		height: 30px;
	}
	.action-button.past-report-view {
		height: 20px !important;
		font-size: 10px !important;
		line-height: 20px !important;
	}

	#report-comm-summary {
		font-size: 12.5px;
	}
	#report-comm-summary>#spiff-commission-data {
		height: 100%;
	}

	.monthly-report-closedate>div {
		font-size: 15px !important;
		font-style: italic !important;
		margin-bottom: 5px !important;
	}

	.card-content.report-view-table-cont {
		height: 64vh !important;
	}
	.report-view-table-cont>.data-table {
		height: 14vh !important;
	}
	.report-view-table-cont>.tablerow.header {
		height: 4vh !important;
	}
}