/*Print layout customization*/
@media print {
    @page {
        margin: 0.25in;
    }
    body {
        margin: 0.25in;
        background-color: #fff !important;
    }
    .report-print-cont {
        display: grid;
    }
    .spiff-sum-cont {
        page-break-before: always;
    }
    .report-summary {
        page-break-after: always; 
    }
    .card-content, .card {
        background-color: #fff;
    }
    .data-table {
        overflow: visible !important;
    }
	.tech-spiff-summary {
		overflow: visible !important;
	}

    .tablerow {
        height: fit-content !important;
    }

    .tablerow>.row-item {
        overflow: visible !important;
    }

    .report-view-table-cont {
        overflow: visible !important;
        min-height: max-content !important;
        max-height: max-content !important;
    }

    .report-view-table-cont>.data-table {
        height: fit-content !important;
        font-size: 10px;
        padding-left: 0px;
        padding-right: 0px;
    }
    .report-view-table-cont>.data-table>:first-child {
        border-top-right-radius: 0px;
        border-top-left-radius: 0px;
    }
    .report-view-table-cont>.data-table>.tablerow {
        padding: 0px;
        text-align: center !important;
        grid-template-columns: 10% 5% 15% 8% 8% 5% 25% 5% 18%;
    }
    .report-view-table-cont>.data-table>:nth-child(odd) {
        background-color: #ccc;
    }
    .report-view-table-cont>.tablerow.header {
        font-size: 15px;
        text-align: center;
        display: grid;
        padding: 0;
        grid-template-columns: 10% 5% 15% 8% 8% 5% 25% 5% 19%;
        
    }

    .report-view-table-cont>.tablerow.header>:first-child{
        border-top-left-radius: 0px;
        border-bottom-left-radius: 0px;
    }
    .report-view-table-cont>.tablerow.header>:last-child{
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
    }
    
    .card-content.report-view-table-cont {
        padding: 0;
    }

    /*Headers*/
    #spiff-summary-titlebar {
        padding-left: .1in;
        padding-right: .1in;
        display: grid;
        justify-content: center;
        grid-template-columns: 16.5% 66% 17.5%;
        background-color: #103b61;
        color:#fff;
        border-radius: 5px;
    }
    #report-summary-titlebar {
        padding-left: .1in;
        padding-right: .1in;
        display: grid;
        justify-content: center;
        grid-template-columns: 20.5% 62% 17.5%;
        background-color: #103b61;
        color:#fff;
        border-radius: 5px;
    }
    #open-header, #lost-header, #approved-header {
        background-color: rgb(57, 109, 143);
        color:#fff;
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
        padding: 5px;
    }
    #lost-header {
        background-color: rgb(143, 57, 57);
    }
    #approved-header {
        background-color: rgb(57, 143, 64);
    }
    #open-header>div, #lost-header>div, #approved-header>div {
        font-size: 40px !important;
        text-align: left;
        color:#fff;
    }
    #report-comm-summary {
        margin-bottom: 0.1in;
        display: grid;
        height: fit-content;
    }
    #report-print-comm-summary {
        display: grid;
    }
    .comm-type-cont {
        display: flex !important;
        flex-direction: column !important;
        gap:10px !important;
        margin: 0in !important;
        padding: 0.05in !important;
    }
    #report-comm-summary>:first-child,#report-print-comm-summary>:first-child {
        display: contents !important;
    }

    
    .comm-type-form {
        display: flex !important;
        flex-direction: column !important;
        background-color:transparent !important;
        margin: 0 !important;
    }
    .comm-type-form>.form-info-item {
        display: grid;
        grid-template-columns: 90% 10%;
        width: 100%;
        font-size: 12.5px;
    }
    .comm-type-form>.form-info-item>:nth-child(2) {
        text-align: left;
        font-weight: 700;
        font-size: 20px;
    }
    .comm-type-form>.form-info-item>.form-info-title {
        overflow:visible;
        display:flex;
        text-align: left;
    }
    #lost-comm-type, #appr-comm-type {
        display: grid;
    }
    .comm-type-cont>.card-content {
        display: flex;
        flex-direction: column;
        padding: 0;
    }
    #lost-comm-type>div, #appr-comm-type>div {
        font-size: 20px;
        text-align: left;
        padding: 0;
    }
    .card-content.data-table {
        overflow: unset !important;
    }
}