/*Spiff app customization*/

/*Spiff card*/
#spiff-card {
    min-width: 97.5vw;
    min-height:calc(85vh - 120px);
	max-height:calc(85vh - 120px);
    margin-left:1.25vw;
    margin-right:1.25vw;
    margin-top: 1.5vh;
	filter:unset;
	border: 1px solid #ccc;
}
#spiff-card>.card-menubar {
	position: relative;
}

#status-menu {
	position: unset;
	top: 0;
}

#spiff-card-card-content {
    width: 100%;
    height:67vh;
    padding-left: 0px;
    padding-right: 0px;
    margin-bottom: 0px;
    padding-bottom: 0px;
	padding-top: 0px;
}

#spiff-card-card-content>.card-content.data-table {
    height: calc(100% - 80px);
    margin-bottom: 0;
    padding-bottom: 0;
    padding-top: 0;
    margin-top: 0;
    overflow: unset;
}

#spiff-card-card-content>.top-bar {
    width:100%
}
  
#spiff-filter-card {
    min-height: 18vh;
	max-height: 18vh;
    max-width: 60vw;
    min-width: 60vw;
    margin-left:1.5vw;
    margin-right:1.5vw;
    margin-top: 1.5vh;
	z-index: 50;
	filter: unset;
	border: 1px solid #ccc;
}
#spiff-filter-card>.card-menubar {
	position: relative;
}

#spiff-filter-card-card-content {
    width: 95%;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    gap: 20px;
}

/*Actions*/
#spiff-main-page-actions {
    display: flex;
    flex-direction: row-reverse;
    position: absolute;
    right: 2.5vw;
    top: 2.5vh;
    width: 30vw;
}

#spiff-main-page-actions>.action-button {
    width: 250px;
    height: 50px;
    line-height: 50px;
    font-size: 30px;
}

#spiff-tb-actions {
    position: absolute;
    left: 52.5px;
    top: -40px;
    z-index: 200;
}

#spiff-tb-actions>.action-button {
    width: 40px;
    padding: 0;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    filter: invert() !important;
}

#spiff-tb-actions>.action-button:hover {
    width: 40px;
    padding: 0;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    filter: none !important;
}

#open-report-button {
    background-color: #103b61;
}
#open-report-button:hover {
    background-color: #062a49;
}
#create-spiff-button {
    background-color: #d48215;
}
#create-spiff-button:hover {
    background-color: #ac6b18;
}

#tech-spiff-filter-cont>.drop-down.selected {
    height: 25px;
    line-height: 15px;
    width: 100%;
}
#tech-spiff-filter-cont>.drop-down.selected>.dropdown-list {
    height: 16vh;
}
#spiff-filter-card-card-content>.form-info-item {
    display: flex;
	height: 20px;
}
#spiff-filter-card-card-content>.form-info-item>.form-info-title {
    width: 75%;
}
#spiff-filter-card-card-content>.form-info-item>input {
    width: 100%;
	height: 150%;
}

/*SPIFF TECH SUMMARY
====================================
*/
.report-print-cont {
    display: none;
}
/*Report View Card*/
/*Containers*/
#report-view-card {
    min-height: 92.5vh;
    max-height: 92.5vh;
    min-width: 65.5vw;
    max-width: 65.5vw;
}
/*#report-comm-summary {
    height: 11vh;
    padding-top: 2.5px;
    padding-bottom: 0px;
    display: flex;
    font-size: 20px;
}*/
#report-view-actions {
    display: flex;
    gap:10px;
    height: 5vh;
    margin-bottom: 5px;
}

/*Text Size*/
#report-comm-summary>div>.form-info-item {
    font-size: 0.85em;
}

/*Report Tables*/
/*Containers*/
.report-view-table-cont {
    max-height: 69vh;
    padding-top: 0px !important;
    padding-bottom: 0px !important;
}
.report-view-table-cont>.data-table {
    height: 17.8vh !important;
}

/*Table Title Headers*/
#open-header>div, #lost-header>div, #approved-header>div {
    font-size: 17.5px !important;
    text-align: left;
}

/*Table Row Customizations*/
.report-view-table-cont>.tablerow.header {
    height: 2.5vh;
    display: grid;
    font-size: 20px;
    padding: 0;
    grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 20%;
    border-radius: 50px;
}
.report-view-table-cont>.tablerow.header>:first-child{
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.report-view-table-cont>.tablerow.header>:last-child{
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.report-view-table-cont>.data-table>.tablerow {
    display: grid;
    grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 20%;
    overflow: hidden;
    border-radius: 5px;
}
.report-view-table-cont>.data-table>.tablerow:hover {
    background-color: #ccc;
    cursor: pointer;
}
#approved-table-header>div {
    background-color: rgb(57, 143, 64);
    color:#fff
}
#lost-table-header>div {
    background-color: rgb(143, 57, 57);
    color:#fff
}
#open-table-header>div {
    background-color: rgb(57, 109, 143);
    color:#fff
}

/*Table Row Item Customizations*/
.report-view-table-cont>.tablerow.header>div {
    margin: 0;
}
.report-view-table-cont>div>.tablerow>.row-item {
    height: 100%;
}

/*Headers*/
#spiff-summary-titlebar {
    display: none;
    grid-template-columns: 12.5% 75% 12.5%;
}


/*SPIFF EDIT CARD
====================================
*/
.spiff-edit-card {
    min-width: 55vw !important;
    max-width: 55vw !important;
    min-height: 62.5vh !important;
    max-height: 62.5vh !important;
}

.spiff-edit-history-label {
	text-align: left;
	margin-left: 10px;
	font-size: 12.5px;
	font-style: italic;
}
#spiff-edit-status-closed-dialogue-card-content>.action-row {
	align-items: center;
	justify-content: center;
}

.confirm-box {
    min-width: 20vw;
    min-height: 10vh !important;
    max-height: 10vh !important;
    display: grid;
    padding: 10px;
    align-self: center;
    justify-self: center;
}
.confirm-box>.card-content>div {
   padding: 5px;
}

#spiff-edit-edit {
    display: flex;
}

#edit-spiff-card-float {
    z-index: 200 !important;
    top:-40px;
    height: calc(100% + 40px);
}

#edit-spiff-dialogue-closed-status-card-content>.action-row {
    display: flex;
    justify-content: center;
}

#spiff-edit-info {
    display: grid;
    grid-template-rows: repeat(2, 50px);
    grid-auto-flow: column;
    grid-auto-columns: 40% 60%
}

#spiff-edit-info>div {
    width: 100%;
    font-size: 20px;
}
#spiff-edit-info>label>:first-child {
    width: 50%;
}
#spiff-edit-info>label>:nth-child(2) {
    width: 100%;
    font-weight: 600
}

/*Hide the div of the third card*/
#spiff-edit-info>div:nth-child(3) {
    display: block;
}

#spiff-edit-info>div:nth-child(3)>:nth-child(2) {
    height: 100%;
    line-height: 50px;
    text-align: center;
    width: 100%;
    font-size: 25px;
    font-weight: 600
}

/*Edit Spiff Card*/
.comment-card {
    min-height: 35vh !important;
    max-height: 35vh !important;
    width: 30vw !important;
}

.comment-card>.card-content {
    padding: 0;
}

.comment-input {
    max-height: 31vh !important;
    max-width: 40vw !important;
    min-height: 31vh !important;
    min-width: 30vw !important;
    margin: 0;
    padding: 7.5px;
    font-size: large;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.spiff-edit-input-form {
    margin-left: 2.5vw;
    display: grid;
    min-width: 25vw;
    max-width: 25vw;
    min-height: 30vh;
    max-height: 30vh;
    align-content: center;
    gap:3vh;
}

.spiff-edit-input-form>.form-info-item {
    display: grid;
    grid-template-columns: 45% 55%;
    height: 100%;
}
.spiff-edit-input-form>.form-info-item>.dropdown-cont {
    width: 60%;
}

/*Action row*/
.spiff-edit-action {
    font-weight: 550;
}

#edit-form-actions {
    justify-content: flex-start;
    margin-left: 0.5vw;
    width: 35%;
}

#edit-action-cont {
    padding-left: 0;
    display: flex;
}

.paid-warn-text {
    width: 50%;
    height: 30px;
    padding-left: 25px;
}
.paid-warn-text>div {
    font-size: 25px;
    line-height: 30px;
    text-align: left;
    font-style: italic;
    color: rgb(143, 57, 57);
}

#submit-spiff {
    background-color: rgb(50, 93, 187);
    color:rgb(255, 255, 255)
}

#submit-spiff:hover {
    background-color: rgb(47, 77, 141);
}

#cancel-spiff {
    background-color: rgb(199, 84, 64);
}

#cancel-spiff:hover {
    background-color: rgb(155, 66, 50);
}

/*Table customization*/
.action-button.spiff-edit {
    background-color: rgb(133, 105, 209);
    font-weight: 550;
}
.action-button.spiff-edit:hover {
    background-color: rgb(146, 118, 223);
}
.action-button.spiff-approve {
    background-color: rgb(72, 156, 39);
    font-weight: 550;
}
.action-button.spiff-approve:hover {
    background-color: rgb(81, 168, 47);
}
.action-button.spiff-open {
    background-color: rgb(105, 171, 209);
    font-weight: 550;
}
.action-button.spiff-open:hover {
    background-color: rgb(115, 182, 221);
}
.action-button.spiff-deny {
    font-weight: 550;
}
#spiff-table-cont {
    overflow-y: overlay !important;
}
#spiff-table-cont>.tablerow {
    height: 40px;
}
/*Customize the rows & header to match*/
#spiff-table-cont>.tablerow, #spiff-table-cont>.tablerow, #spiff-card-card-content>.tablerow.header {
    grid-template-columns: 6% 9% 6% 15% 6% 6% 9% 12% 18% 13%;
    height: 10vh;
    border-radius: 5px;
}
#spiff-card-card-content>.tablerow.header {
    height: 40px;
}
#spiff-card-card-content>.tablerow {
    font-weight: 600;
    height: 2vh;
}
#spiff-table-cont>.tablerow:hover {
    background-color: #ccc;
}

/*Header row customizations*/
#spiff-card-card-content>.tablerow.header {
    background-color: #103b61; /*rgb(20, 117, 202)*/
    border-radius: 0px;
}
#spiff-card-card-content>.tablerow.header>div {
    margin: 0px;
    background-color: transparent;
    color: rgb(255,255,255);
}

/*Approved row customization*/


/*Menu bar customization*/
#status-menu>.menu-button {
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    font-weight: 900;
}
/*Open tab*/
#status-menu>:nth-child(1) {
    background-color: #114069;
}
#status-menu>:nth-child(1).selected {
    background-color: #062a49;
}
/*Lost tab*/
#status-menu>:nth-child(2) {
    background-color: #6e2828;
}
#status-menu>:nth-child(2).selected {
    background-color: #4d1919;
}
/*Approved tab*/
#status-menu>:nth-child(3) {
	background-color: #335856;
}
#status-menu>:nth-child(3).selected {
    background-color: #254746;
}

#status-menu>:nth-child(4) {
    display: none;
}

#spiff-card-card-content>.tablerow.header {
	background-color: #efefef;
	border-bottom: 1px solid #ccc;
}
#spiff-card-card-content>.tablerow.header>div {
	color: #242424
}

/*Commission Data Summary customization*/
#spiff-comm-data-cont {
    display: grid;
}
#spiff-commission-data {
    display: grid;
    align-content: center;
    width: 23vw;
    padding: 10px;
    height: 10vh;
}

#spiff-commission-data>.form-info-item {
    display: grid;
    grid-template-columns: 75% 25%;
    font-size: 25px;
}

/*Dollar sign for commission amount*/
#spiff-commission-data>:first-child>:nth-child(2)::before {
    content: '$';
}

#commission-form-total>:nth-child(2) {
    color:rgb(54, 99, 54)
}

#comm-summary-id {
    text-align: left;
    color:rgb(30, 30, 30);
    font-style: italic;
}

/*Windows 125%/150% Scaling CSS*/
@media screen and (max-width: 1550px) {
	/*SPIFF CARD*/
	#spiff-card {
		min-height: calc(82.5vh - 120px);
		max-height: calc(82.5vh - 120px);
	}
	#spiff-card>.card-menubar {
		height: 30px;
		line-height: 12.5px;
	}
	#spiff-card-card-content {
		height: 58vh;
	}
	#status-menu {
		top:30px;
	}
	.action-button.spiff-edit,
	.action-button.spiff-deny,
	.action-button.spiff-approve {
		font-size: 10px;
	}
	#spiff-table-cont>.tablerow {
		font-size: 12.5px;
	}

	/*FILTER CARD*/
	#spiff-filter-card {
		max-height: 27vh;
	}
	#spiff-filter-card>.card-menubar {
		height: 30px;
		line-height: 12.5px;
	}
	#tech-spiff-filter-cont>.dropdown-cont,
	#tech-spiff-filter-cont>.dropdown-cont>.dropdown-option.drop-down.selected {
		height: 112.5%;
		line-height: 100%;
	}


	/*EDIT CARD*/
	.card.spiff-edit-card {
		max-height: fit-content !important;
		max-width: fit-content !important;
	}
	#spiff-edit-cancel-dialogue, #edit-spiff-dialogue {
		max-height: fit-content !important;
	}

	

	/*COMMISSION DATA*/
	#spiff-comm-data-cont {
		height: 12vh;
	}
	#spiff-commission-data {
		height: 18vh;
	}
	#spiff-commission-data>.form-info-item {
		font-size: 20px;
	}
}


